console.log('index.js');
import interfaceUnit from '../../js/interfaceUnit.js'
import hexGrid from '../../js/new_hexGrid.js'
import mobileMenu from '../../js/mobileMenu.js'
import $ from 'jquery'

var popup = function(){
	var s = {
		tab: '.popup-tab',
		content: '.popup-content'
	}
	var tab = $(s.tab);
	var conent = $(s.window);
	var timer
	return {
		main: function(){
			var self = this;
			$(s.tab).on('mouseover', function(event){
				event.stopPropagation();
				var id = $(this).data('id');
				console.log('------------',id)
				$(window).trigger('closePopoup')
				self.showContent(id)
				$(window).trigger('hexGrid')
				clearTimeout(timer)
			})

			$('.popup-content').on('mouseover', function(event){
				event.stopPropagation();
				console.log('-------contetn')
				clearTimeout(timer)
			})

			$(window).on('mouseover', function(event){
				console.log('------- leave')
				clearTimeout(timer)
				timer = setTimeout(function(){
					$(window).trigger('closePopoup')
				}, 500)
			})
			$(window).on('resize', function(){
					$(window).trigger('closePopoup')
			})
		},
		showContent: function(id){
			$('#'+id).show()
		},
		events: function(){
			$(window).on('closePopoup', function(){
				$(s.content).hide()
				console.log('close')
			})
		},
		regScrol: function(){
			$('.header__nav-link--reg').on('click', function(){
				// $(window).scrollTo('.sample')
			$([document.documentElement, document.body]).animate({
				scrollTop: $(".sample").offset().top - 20
			}, 500);
			})

		},
		init: function(){
			console.log('---------popup')
			this.main();
			this.events();
			this.regScrol();
		}
	}
}

console.clear();

$(document).ready(function(){
	interfaceUnit().init();
	mobileMenu().init();
	hexGrid().init();
	$('#hero-slider').slick({
		infinite: true,
		dots: true,
		arrows: false,
		// centerMode: true
	})
	popup().init()
})



